import React, { PropTypes, Component  } from 'react';
import KlassName from 'teapan/klassName';

class DropdownTrigger extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		const props = this.props;
		const {
			className,
			activated,
			children,
			onClick,
			...others
		} = props;

		const klassName = KlassName(
			'dropdown-trigger',
			{
				['activated']:activated
			},
			className
		);

		return (
			<div {...others} onClick={onClick} className={klassName}>
				{children}
			</div>
		);
	}
};

DropdownTrigger.propTypes = {
	activated: PropTypes.bool,
	type: PropTypes.oneOf(['warp','menu','navbar']),

	className: PropTypes.string,
	children: PropTypes.any,
	onClick: PropTypes.func
};

DropdownTrigger.defaultProps = {
	type: 'warp',
};

export default DropdownTrigger;